'use client'

import { useState, useEffect, useRef } from 'react'
import { useAuth } from '@/lib/auth/AuthContext'
import { useSocket } from '@/lib/socket/SocketContext'
import { conversationsApi, type Conversation, type ConversationDetails } from '@/lib/api/services'
import { LoadingSpinner } from '@/components/ui/LoadingSpinner'
import { ConversationList } from '@/components/agent/ConversationList'
import { ConversationView } from '@/components/agent/ConversationView'
import { ConversationSearch } from '@/components/agent/ConversationSearch'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { ResizablePanelGroup, ResizablePanel, ResizableHandle } from '@/components/ui/resizable'
import {
  Search,
  Filter,
  MessageSquare,
  Circle,
} from 'lucide-react'

// Use the API types instead of local interfaces


export default function ConversationsPage() {
  const { user } = useAuth()
  const { socket, connected } = useSocket()
  const [conversations, setConversations] = useState<Conversation[]>([])
  const [selectedConversation, setSelectedConversation] = useState<Conversation | null>(null)
  const [conversationDetails, setConversationDetails] = useState<ConversationDetails | null>(null)
  const [loading, setLoading] = useState(true)
  const [loadingMessages, setLoadingMessages] = useState(false)
  const [searchQuery, setSearchQuery] = useState('')
  const [statusFilter, setStatusFilter] = useState<string>('all')
  const [showSearch, setShowSearch] = useState(false)
  const [error, setError] = useState<string | null>(null)

  // Filter conversations based on search and status
  const filteredConversations = conversations.filter(conv => {
    const matchesSearch = !searchQuery || 
      conv.leadName.toLowerCase().includes(searchQuery.toLowerCase()) ||
      conv.phoneNumber.includes(searchQuery) ||
      conv.lastMessage.toLowerCase().includes(searchQuery.toLowerCase())
    
    const matchesStatus = statusFilter === 'all' || conv.status === statusFilter
    
    return matchesSearch && matchesStatus
  })

  // Real-time message handling
  useEffect(() => {
    if (!socket || !connected) return

    const handleNewMessage = (data: any) => {
      // Update conversation list
      setConversations(prev => prev.map(conv => 
        conv.id === data.conversationId 
          ? {
              ...conv,
              lastMessage: data.message,
              lastMessageTime: 'Just now',
              unreadCount: conv.unreadCount + 1
            }
          : conv
      ))

      // Add message to current conversation
      if (selectedConversation?.id === data.conversationId) {
        setMessages(prev => [...prev, {
          id: data.messageId,
          conversationId: data.conversationId,
          sender: data.sender,
          message: data.message,
          timestamp: data.timestamp,
          messageType: data.messageType || 'text',
          status: 'delivered'
        }])
      }
    }

    socket.on('new_message', handleNewMessage)
    socket.on('message_status_update', (data: any) => {
      setMessages(prev => prev.map(msg => 
        msg.id === data.messageId 
          ? { ...msg, status: data.status }
          : msg
      ))
    })

    return () => {
      socket.off('new_message', handleNewMessage)
      socket.off('message_status_update')
    }
  }, [socket, connected, selectedConversation])

  const handleConversationSelect = (conversation: Conversation) => {
    setSelectedConversation(conversation)
    
    // Mark as read
    setConversations(prev => prev.map(conv => 
      conv.id === conversation.id 
        ? { ...conv, unreadCount: 0 }
        : conv
    ))

    // Load messages for this conversation
    // In real app, this would be an API call
    const conversationMessages = mockMessages.filter(msg => msg.conversationId === conversation.id)
    setMessages(conversationMessages)
  }

  const handleSendMessage = (message: string) => {
    if (!selectedConversation) return

    const newMessage: Message = {
      id: Date.now().toString(),
      conversationId: selectedConversation.id,
      sender: 'agent',
      message,
      timestamp: new Date().toISOString(),
      messageType: 'text',
      status: 'sent'
    }

    setMessages(prev => [...prev, newMessage])
    
    // Update conversation list
    setConversations(prev => prev.map(conv => 
      conv.id === selectedConversation.id 
        ? {
            ...conv,
            lastMessage: message,
            lastMessageTime: 'Just now'
          }
        : conv
    ))

    // Send via socket
    if (socket && connected) {
      socket.emit('send_message', {
        conversationId: selectedConversation.id,
        leadId: selectedConversation.leadId,
        message,
        agentId: user?.id
      })
    }
  }

  return (
    <Card className="h-full overflow-hidden">
      <ResizablePanelGroup direction="horizontal" className="h-full">
        {/* Conversations List Panel */}
        <ResizablePanel defaultSize={30} minSize={25} maxSize={50}>
          <div className="h-full flex flex-col">
            {/* Header */}
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between mb-4">
                <CardTitle className="flex items-center gap-2">
                  <MessageSquare className="h-5 w-5" />
                  Conversations
                </CardTitle>
                <div className="flex items-center space-x-2">
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={() => setShowSearch(!showSearch)}
                  >
                    <Search className="h-4 w-4" />
                  </Button>
                  <Button variant="ghost" size="sm">
                    <Filter className="h-4 w-4" />
                  </Button>
                </div>
              </div>

              {/* Search */}
              {showSearch && (
                <ConversationSearch
                  searchQuery={searchQuery}
                  onSearchChange={setSearchQuery}
                  statusFilter={statusFilter}
                  onStatusFilterChange={setStatusFilter}
                />
              )}

              {/* Stats */}
              <div className="flex items-center justify-between text-sm text-muted-foreground">
                <span>{filteredConversations.length} conversations</span>
                <div className="flex items-center gap-1">
                  <Circle className={`w-2 h-2 ${connected ? 'fill-green-500 text-green-500' : 'fill-red-500 text-red-500'}`} />
                  <Badge variant="outline" className="text-xs">
                    {connected ? 'Online' : 'Offline'}
                  </Badge>
                </div>
              </div>
            </CardHeader>

            {/* Conversation List */}
            <CardContent className="flex-1 overflow-y-auto p-0">
              {loading ? (
                <div className="flex items-center justify-center h-32">
                  <LoadingSpinner />
                </div>
              ) : filteredConversations.length > 0 ? (
                <ConversationList
                  conversations={filteredConversations}
                  selectedConversation={selectedConversation}
                  onConversationSelect={handleConversationSelect}
                />
              ) : (
                <div className="flex flex-col items-center justify-center h-32 text-muted-foreground p-6">
                  <MessageSquare className="h-8 w-8 mb-2" />
                  <p className="text-sm">No conversations found</p>
                </div>
              )}
            </CardContent>
          </div>
        </ResizablePanel>

        <ResizableHandle withHandle />

        {/* Conversation View Panel */}
        <ResizablePanel defaultSize={70}>
          <div className="h-full flex flex-col">
            {selectedConversation ? (
              <ConversationView
                conversation={selectedConversation}
                messages={messages}
                onSendMessage={handleSendMessage}
                connected={connected}
              />
            ) : (
              <div className="flex-1 flex items-center justify-center">
                <div className="text-center">
                  <MessageSquare className="h-12 w-12 mx-auto mb-4 text-muted-foreground/50" />
                  <h3 className="text-lg font-medium mb-2">Select a conversation</h3>
                  <p className="text-sm text-muted-foreground">Choose a conversation from the list to start messaging</p>
                </div>
              </div>
            )}
          </div>
        </ResizablePanel>
      </ResizablePanelGroup>
    </Card>
  )
}
